<template>
<div class="container">
            <div v-if="observacion!=''" class="alert alert-warning">   
                {{observacion}}
            </div>
            
            <div class="table-responsive">
                <table class="table table-bordered text-center">
                    <thead>
                        <tr class="bg-light-gray">
                            <th class="text-uppercase">{{etiquetaColumna}}
                            </th>
                            <th class="text-uppercase" v-for="columna in columnas">
                                {{columna.label}}
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="fila in filas">
                            <td class="align-middle">{{fila.label}}</td>
                            <evento v-bind:key="columna.id+'_'+fila.id" 
                                    v-for="columna in columnas" 
                                    :fila="fila.id"
                                    :columna="columna.id"
                                    :registro='obtener_asignacion(fila.id,columna.id)'
                                    @captura_observacion="mostrar_observacion"
                                    @captura_registro="registrar_asignacion"
                                    >
                            </evento>
                        </tr>
                    </tbody>
                </table>
            </div>

        </div>    
</template>

<script>
import Event from './Event.vue';
export default {
  name: 'app',
  data () {
    return {
      observacion:"",
      etiquetaColumna: 'Estaciones'
      
    }
  },
  props:["filas","columnas","asignaciones"],
  components:{
    "evento":Event
  },
  methods:{
    obtener_asignacion: function(fila, columna){         
            var resultado = this.asignaciones.filter(function (item) {
                if(item.fila == fila && item.columna == columna){
                    return true;
                } else {
                    return false;
                }
            });

            if(resultado.length == 0){
                return null;
            } else {
                return resultado[0];
            }
        },
        registrar_asignacion: function(fila, columna){
            this.$emit('celda_activa',fila,columna);
        },
        
        mostrar_observacion: function(mensaje){
            this.observacion=mensaje;
        }
  }
}
</script>

<style>
body{
    margin-top:20px;
}

.table-bordered thead td, .table-bordered thead th {
    border-bottom-width: 2px;
}
.table thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
}
.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}


.bg-sky.box-shadow {
    box-shadow: 0px 5px 0px 0px #00a2a7
}

.bg-orange.box-shadow {
    box-shadow: 0px 5px 0px 0px #af4305
}

.bg-green.box-shadow {
    box-shadow: 0px 5px 0px 0px #4ca520
}

.bg-yellow.box-shadow {
    box-shadow: 0px 5px 0px 0px #dcbf02
}

.bg-pink.box-shadow {
    box-shadow: 0px 5px 0px 0px #e82d8b
}

.bg-purple.box-shadow {
    box-shadow: 0px 5px 0px 0px #8343e8
}

.bg-lightred.box-shadow {
    box-shadow: 0px 5px 0px 0px #d84213
}




.padding-15px-lr {
    padding-left: 15px;
    padding-right: 15px;
}
.padding-5px-tb {
    padding-top: 5px;
    padding-bottom: 5px;
}
.margin-10px-bottom {
    margin-bottom: 10px;
}
.border-radius-5 {
    border-radius: 5px;
}

.margin-10px-top {
    margin-top: 10px;
}
.font-size14 {
    font-size: 14px;
}

.text-light-gray {
    color: #d6d5d5;
}
.font-size13 {
    font-size: 13px;
}

.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}
.table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
}
</style>




