<template>
    <div>
        <datepicker 
        :language="es"
        input-class="form-control"
        format="yyyy-MM-dd"
        @input="ver_dia"
        ></datepicker>  
        <tabla :columnas="columnas"
                :filas="filas"
                :asignaciones="asignaciones"
                @celda_activa="mostrar_modal"
        ></tabla>
        <div class="modal fade" tabindex="-1" role="dialog" id="modal_asignacion">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">Agregar asignacin</h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <form>
                                <div class="form-group">
                                    <label for="">Cliente</label>
                                    <select v-model="nueva_asignacion.socio" class="form-control">
                                        <option v-for="socio in socios" :value="socio.idsocio">{{socio.nombre}}</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <label for="">Personal</label>
                                    <input v-model="nueva_asignacion.personal" type="text" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="">Placa</label>
                                    <input v-model="nueva_asignacion.placa" type="text" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="">Modelo</label>
                                    <input v-model="nueva_asignacion.modelo" type="text" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="">Ao</label>
                                    <input v-model="nueva_asignacion.anio" type="text" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="">Tipo</label>
                                    <select  v-model="nueva_asignacion.idtipo" class="form-control">
                                        <option v-for="tipo in tipos" :value="tipo.idtiposervicio">{{tipo.nombre}}</option>
                                    </select>
                                </div> 
                            </form>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-primary" @click="insertar_asignacion()">Registrar</button>
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                        </div>
                    </div>
                </div>
            </div>
    </div>
</template>

<script>
    import TimeTable from './TimeTable.vue';
    import Datepicker from 'vuejs-datepicker';
    import {es} from 'vuejs-datepicker/dist/locale';
    import axios from 'axios';
    export default{
        data(){
            return{
                columnas:columnas_iniciales,
                filas:filas_iniciales,
                asignaciones: asignaciones_iniciales,
                es:es,
                url:url_envio,
                url_insertar:url_envio2,
                sincrono:bandera_sincrono,
                token:tk,
                nueva_asignacion: {
                    fila: 1,
                    columna: 1,
                    socio: 2,
                    personal:'',
                    placa:'',
                    modelo:'',
                    anio:'',
                    idtipo: 2,
                    fecha:''
                },
                socios:lista_socios,
                tipos:lista_tipos
            }
        },
        components:{
            "tabla":TimeTable,
            datepicker:Datepicker
        },
        methods:{
            formato_fecha:function(fecha){
                var mes = fecha.getMonth()+1;
                var label_mes = mes.toString();

                var dia = fecha.getDate();
                var label_dia = dia.toString();

                if (mes<10){
                    label_mes = '0'+label_mes;
                }
                if (dia<10){
                    label_dia = '0'+label_dia;
                }
                return fecha.getFullYear()+"-"+label_mes+"-"+label_dia;
            },
            ver_dia:function(fecha){
                this.nueva_asignacion.fecha=this.formato_fecha(fecha);
                if (this.sincrono) {
                    document.querySelector('#oculto').value=this.formato_fecha(fecha);
                    document.frm.submit();
                } else {
                    var datos ={
                        fecha:this.formato_fecha(fecha),
                        _token:this.token
                    };
                    axios.post(this.url,datos).then((result)=>{
                        for(var i=0;i<result.data.length;i++){
                            this.asignaciones.push(result.data[i]);
                        }
                    }); 
                } 
            },
            insertar_asignacion: function(){
                var datos ={
                        servicio:this.nueva_asignacion,
                        _token:this.token
                    };
                    axios.post(this.url_insertar,datos).then((result)=>{
                        if(result.data.status=='Error'){
                            alert(result.data.mensaje);
                            $("#modal_asignacion").modal('hide');
                        }else{
                            alert('se guardo');
                            this.asignaciones.push({
                                fila:this.nueva_asignacion.fila,
                                columna:this.nueva_asignacion.columna,
                                cliente:result.data.cliente,
                                tipo:result.data.tipo
                            });
                            $("#modal_asignacion").modal('hide');
                        }
                        
                    }).catch(function(error){
                        alert('error');
                        console.log(error);
                    });
                
            },
            mostrar_modal:function(fila,columna){
                this.nueva_asignacion.fila = fila;
                this.nueva_asignacion.columna = columna;
                $("#modal_asignacion").modal('show');
            }
        }
    }
</script>

<style>

</style>



