<template>
    <td
    :class="formatoSinAsig(registro)"
    @click="registrar_celda"
    >
        <div v-if="registro">
            <span class=" padding-5px-tb padding-15px-lr border-radius-5 margin-10px-bottom text-white font-size16 xd-font-size13"
            :class="formato_tipo(registro.tipo)"
            >{{registro.tipo}}</span>
            <div class="margin-10px-top font-size14">
                {{registro.cliente}}
            </div>
            <input type="text" class="form-control" @input="aviso_papa($event)">
        </div>
    </td>
</template>

<script>
    export default{
        data(){
            return {
                dato1:'dato interno'
            }
            
        },
        props:["registro","fila","columna"],
        methods:{
            formato_tipo: function(tipo){
            switch (tipo) {
                case 'Normal':
                    return {'bg-sky': true};
                    break;
                case 'Diamante':
                    return {'bg-green': true};
                    break;
                case 'VIP':
                    return {'bg-purple': true};
                    break;
                default:
                    break;
                 }
             },
            formatoSinAsig: function(asignacion){
            if(!asignacion){
                return {'bg-light-gray': true}; 
                }
            },
            registrar_celda:function(){
                if(!this.registro){
                    this.$emit("captura_registro",this.fila,this.columna);
                }
                
            },
            aviso_papa:function(event){
                this.$emit("captura_observacion",event.target.value);
            }
        }
       
    }
    
</script>

<style>
    .bg-light-gray {
     background-color: #f7f7f7;
    }
    .bg-sky {
        background-color: #02c2c7
    }

    .bg-orange {
        background-color: #e95601
    }

    .bg-green {
        background-color: #5bbd2a
    }

    .bg-yellow {
        background-color: #f0d001
    }

    .bg-pink {
        background-color: #ff48a4
    }

    .bg-purple {
        background-color: #9d60ff
    }

    .bg-lightred {
        background-color: #ff5722
    }
</style>


